<template lang="pug">
    div(class="page")
        Header
        section(class='careers')
            div(class='container')
                div(class='title')
                    h2: span {{ $t("careers") }}
                div(class='careers-box wow fadeInUp' data-wow-duration='1s' v-for="career in CareersData.careers", :key="career.id")
                    div(class='details')
                        h3 {{ career.title }}
                        div(v-html="career.body")
                        .clickable
                            router-link(:to="`/careers/${career.url}`" class="btn hvr-sweep-to-bottom") {{ $t("apply") }}
        Footer
</template>

<script>
import { mapState } from "vuex";
// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

export default {
    name: "CareersPage",
    components: {
        Header,
        Footer,
    },
    mounted() {
        this.$store.dispatch("getCareersData");
    },
    computed: {
        ...mapState(["CareersData"]),
    },
};
</script>
